.main {
    background-color:#f5f5f5;
    padding: 20px 30px;
    border-bottom: 1px solid #DDDDDD;
    overflow-x: hidden;
}
.section {
    margin-bottom: 20px;    
}

.full-width {
    width: 100%;
}

.title {
    text-align: center;
    font-size: 35px;
}

.valign-text-container {
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align:center;
        align-items:center;
    font-size:18px;
}

.flex-first {
    -ms-flex-order: 1;
        order: 1;
}

.flex-last {
    -ms-flex-order: 2;
        order: 2;
}

@media (max-width: 767px) {
    .title {
        font-size: 22px;
    }
    .flex-first {
        -ms-flex-order: 0;
            order: 0;
    }
    
    .flex-last {
        -ms-flex-order: 0;
            order: 0;
    }
}


